article {
	margin-top: 142px;
	@include md {
		margin-top: 50px; } }
.article {
	background: #fff;
	color: $darkblue;
	border-radius: 16px;
	// max-height: 1160px
	overflow: hidden;
	&__heading {
		@include f(60px, 600);
		padding: 80px 100px 0;
		@include md {
			@include f(48px, 600);
			padding: 40px;
			padding-bottom: 0; } } }

.post {
	display: flex;
	max-width: 1100px;
	margin: 0 auto;
	flex-direction: column;
	flex-wrap: wrap;
	max-height: 1120px;
	padding: 35px;
	align-items: flex-end;
	@include md {
		max-height: 950px; } }
.post__item {
	display: flex;
	max-width: 460px;
	word-wrap: break-word;
	width: 50%;
	margin: 0 18px 45px 0;
	& svg {
		margin-right: 27px;
		@include md {
			margin-right: 10px; } }
	& p {
		@include f(18px, 300);
		max-width: 80%;
		margin-top: -10px;
		line-height: 2;
		@include md {
			@include f(14px, 300); } }
	@include md {
		margin-right: 0; } }
