.forecast {
	margin: 140px auto 0;
	position: relative;
	&__container {
		max-width: 1050px; }
	&__heading {
		@include f(60px, 600);
		padding: 0 20px 2px;
		@include md {
			@include f(48px, 600); } }
	&__desc {
		@include f(24px, 600);
		padding: 0 20px;
		@include md {
			@include f(18px, 600); } }
	&__currency {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: 40px;
		margin-left: 13px;
		& li {
			width: 136px;
			height: 84px;
			@include md {
				max-width: 100px;
				max-height: 64px; } } }
	&__box1 {
		position: absolute;
		top: -65px;
		right: 7.5%;
		z-index: -1; }
	&__box2 {
		position: absolute;
		top: 60px;
		left: 0;
		z-index: -1;
		@include md {
			top: 1280px;
			left: -150px;
			z-index: 1; } }
	@include md {
		margin-top: 40px; } }
.currency {
	&__item {
		display: block;
		text-align: center;
		text-transform: uppercase;
		border: 8px solid #223e5c;
		background: transparent;
		max-width: 136px;
		width: 100%;
		max-height: 84px;
		height: 100%;
		// line-height: 68px
		border-radius: 8px;
		@include f(30px, 700);
		color: #fff;
		transition: .3s;
		@include hover {
			background: $navy;
			border: 8px solid $navy; }
		@include md {
			@include f(18px, 700); }
		// 	max-width: 100px
 } }		// 	max-height: 64px

.graph {
	margin: 40px auto 0;
	position: relative;
	&__img {
		display: block;
		margin: 0 auto;
		position: relative;
		z-index: -2;
		width: 100%;
		padding: 0 30px; } }

.indicators {
	display: flex;
	position: absolute;
	left: 27.4%;
	top: 5.5%;
	line-height: 1.3;
	&__item {
		margin-right: 45px; }
	@include md {
		left: 10%; } }

.indicator {
	&__main {
		text-transform: uppercase;
		@include f(24px, 700);
		@include md {
			@include f(20px, 700); }
		&_centered {
			line-height: 2.6; } }
	&__aside {
		@include f(12px, 500);
		text-align: right;
		margin-left: -15px;
		@include md {
			@include f(10px, 500); }
		& span {
			color: $green; }
		&_arrow {
			margin-bottom: 2px; } } }
